.header {
  width: 100%;
  overflow: hidden;
  min-height: $header-size;
  background: $rhino;
  font-size: 1.5em;
  margin-bottom: .5rem;
  position: relative;
}

.nav {
  &-icon {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    cursor: pointer;
    top: 2rem;
    right: 1rem;

    i {
      font-size: 2.5rem;
      color: white;
    }

    @include min-mq(large) {
      display: none;
    }
  }
}
